:root{
    --primaryColor : #90a959;
    --secondColor : #E98872;
    --mainWhite : #fff;
    --mainBlack : #222;
    --mainRed : #c45151;
    --mainGreen : #4caf50;
}

*{
    margin:0;
    box-sizing: border-box;
}

body{
    background-color: var(--secondColor);
    color : var(--mainBlack);
}
h1{
    margin: 1rem 0; 
}